<template>
    <div>
        <h1></h1>
        <el-form ref="form" :model="form_data" label-width="120px" :label-position="'left'">
             <el-form-item label="">
                <el-input v-model="form_data.name"></el-input>
              </el-form-item>

             <el-form-item label="">
                <el-input v-model="form_data.des"></el-input>
            </el-form-item>


            <el-form-item label="">
                <el-switch v-model="form_data.private"></el-switch>
            </el-form-item>


            <el-form-item label="">
                <el-select v-model="form_data.Line" placeholder="">
                    <el-option label="APP" value="app"></el-option>
                    <el-option label="WEB" value="web"></el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="form_data.email_to"></el-input>
            </el-form-item>

            <el-form-item label="IP">
                <el-input v-model="form_data.sql_host" style="width: 300px"></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="form_data.sql_port" style="width: 192.5px"></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="form_data.sql_user" style="width: 300px"></el-input>
            </el-form-item>


            <el-form-item label="">
                <el-input v-model="form_data.sql_pwd" style="width: 300px"></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="form_data.sql_db" style="width: 300px"></el-input>
            </el-form-item>




            <el-form-item>
                <el-button type="primary" @click="Save_project"></el-button>
            </el-form-item>

        </el-form>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "Project_set",
        data(){
           return{
               form_data:{
                   name:'',
                   des:'',
                   private:false,
                   Line:'',
                   email_to:'',
                   sql_host:'',
                   sql_port:'',
                   sql_user:'',
                   sql_pwd:'',
                   sql_db:'',
               }
           }
        },
        methods:{
            Save_project(){
                axios.post('http://localhost:8000/save_project/',this.form_data).then(
                    this.$message({
                        message:"",
                        type:'success',
                    })
                )
            }
        },
        mounted:function() {
            axios.get('http://localhost:8000/get_project_detail/',{
                params:{
                    id:this.project_id
                }
            }).then(res=>{
                this.form_data = res.data
            })
        },
        props:["project_id"]
    }
</script>

