<template>
    <div>
        <el-container>
            <el-aside style=" width: 200px;">
                <Menu></Menu>
            </el-aside>
            <el-main>
                <h1> <span style="color: grey;font-size: xx-small"></span></h1>

                <el-table
                    :data="env_list_data"
                    stripe
                    border
                    style="min-width: 100%;min-height: 100%;overflow-y: auto"
                >
                    <el-table-column
                        prop="id"
                        label="ID"
                        width="100"
                    >
                    </el-table-column>

                    <el-table-column
                        prop="type"
                        label=""
                        width="200"
                    >
                        <template slot-scope="scope">
                            <el-tag>{{ scope.row.type }}</el-tag>
                        </template>
                    </el-table-column>

                    <el-table-column
                        prop="host"
                        label=""
                        width="300"
                    >

                    </el-table-column>

                    <el-table-column
                        prop="des"
                        label=""
                    >

                    </el-table-column>

                    <el-table-column>
                        <template slot="header">
                            <el-button @click="dialogFormVisible=true"></el-button>
                        </template>
                        <template slot-scope="scope">
                            <el-button size="mini" type="danger"  @click="Delete_env(scope.row.id)"></el-button>
                        </template>
                    </el-table-column>

                </el-table>
            </el-main>
        </el-container>

        <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="host" label-width="80px">
                    <el-input v-model="form_data.host"></el-input>
                </el-form-item>

                <el-form-item label="" label-width="80px">
                    <el-input v-model="form_data.des"></el-input>
                </el-form-item>

                <el-form-item label="" label-width="80px">
                    <el-input v-model="form_data.type"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="Add_env()"> </el-button>
              </div>
        </el-dialog>

    </div>
</template>

<script>
    import Menu from '../components/Menu'
    import axios from 'axios'
    export default {
        name: "Env_list",
        data(){
          return{
              dialogFormVisible:false,
              env_list_data:[],
              form_data:{
                  host:'http://',
                  type:'',
                  des:''
              }

            }
        },
        methods:{
          Add_env(){
              this.dialogFormVisible = false;
              axios.post('http://localhost:8000/add_env/',this.form_data).then(res=>{
                  this.env_list_data = res.data
                  this.$message({
                      message:"",
                      type:'success'
                  })
              })
          },
            Delete_env(env_id){
              axios.get('http://localhost:8000/delete_env/',{
                  params:{
                      env_id : env_id
                  }
              }).then(res=>{
                  this.env_list_data = res.data;
              })
            }
        },
        mounted(){
            axios.get('http://localhost:8000/get_env_list/').then(
                res=>{
                    this.env_list_data = res.data;
                }
            )
        },
        components:{
            Menu
        }
    }
</script>

<style scoped>
 .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;

  }
  .el-main {
    background-color: white;
    color: #333;
  }
</style>